import { Space, Card} from "antd";
import { Form,  Input,  Row, Col,  Checkbox,  Button,  message,} from 'antd';
import { useState ,useRef} from "react";
import {setVerifyCode, createUser} from "../api/User"
import Loading from "./Loading";
import sha256 from 'crypto-js/sha256'
var modal = document.getElementById("myModal");



const Register = ( {setLoginOrRegister, modal} ) => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [nickname, setNickname] = useState("");
  // const [captcha, setCaptcha] = useState(_uuid());
  const [captcha, setCaptcha] = useState("");
  // const modal =  useRef(null);


  const submit = async(values) => {
    var thisModal = modal.current;
    thisModal.style.display = "block";
    const hashDigest = sha256(password)
    let hash = ''
    for (let i=0; i<hashDigest.words.length; i++) {
      hash += hashDigest.words[i].toString()
    }
    const result = await createUser(email,captcha,nickname,hash)

    switch(result){
      case "Didn't click verify button!":
        message.error("", 3)
        break;
      case "Wrong verify code.":
        message.error("", 3)
        break;
      case "username exist!":
        message.error("", 3)
        break;
      case "mail exist!":
        message.error("", 3)
        break;
      case "User created.":
        message.success("", 3)
        setLoginOrRegister("login");
        break;
    }

      thisModal.style.display = "none";
  };
		

	const onClickLogin =(e)=> {
		e.preventDefault();
		setLoginOrRegister("login");
	}
	const getCode = async () => {
		let emailLower = email.toLowerCase();
    let emailDomain = "@ntu.edu.tw";
    let emailDomain2 = "@g.ntu.edu.tw"
    if(emailLower.indexOf("@") === -1 || (emailLower.indexOf("edu") === -1 && emailLower.indexOf("com") === -1)){
      message.error("email")
    }
    else if (emailLower.indexOf(",") !== -1 || emailLower.indexOf("#") !== -1 || emailLower.indexOf("$") !== -1 ||
            emailLower.indexOf("%") !== -1  || emailLower.indexOf("&") !== -1 || emailLower.indexOf("^") !== -1){
      message.error("email")      
    }
    else if (emailLower.indexOf(emailDomain) === -1 && emailLower.indexOf(emailDomain2) === -1){
      message.error('');
    }
    else if(emailLower.split('@') <= 2){
      message.error("email")
    }
    else {
      var thisModal = modal.current;
      thisModal.style.display = "block";
      const data = await setVerifyCode(email);
      thisModal.style.display = "none";
      if(data === "User mail exist")
        message.error("")
      else if (data === "Good.")
        message.success("")
    }
  }

  const onChangeEmail = (e) =>{
    setEmail(e.target.value);
  }
  const onChangePassword = (e) =>{
    setPassword(e.target.value);
  }  
  const onChangeNickname = (e) =>{
    setNickname(e.target.value);
  }
  const onChangeCaptcha = (e) =>{
    setCaptcha(e.target.value);
  }

	const [form] = Form.useForm();

	const formItemLayout = {
    labelCol: {
      xs: {
      span: 24,
      },
      sm: {
      span: 8,
      },
    },
    wrapperCol: {
      xs: {
      span: 24,
      },
      sm: {
      span: 16,
      },
    },
	};
	const tailFormItemLayout = {
    wrapperCol: {
      xs: {
      span: 24,
      offset: 0,
      },
      sm: {
      span: 16,
      offset: 8,
      },
    },
	};

  return (
    <>
    <div className="site-card-border-less-wrapper">
      <div className="card" style={{margin:"auto"}}>
      <Card  title="" bordered={true} style={{ width: 400 }}>   
      <Form
        {...formItemLayout}
        form={form}
        name="register"
        onFinish={submit}
        scrollToFirstError
      >
        <Form.Item
          name="email"
          label="E-mail"
          rules={[
            {
              type: 'email',
              message: 'email!',
            },
            {
              required: true,
              message: 'email!',
            },
            ({ getFieldValue }) => ({
              validator(_, value) {
                if (!value || getFieldValue('email').indexOf("@ntu.edu.tw") !== -1 || getFieldValue('email').indexOf("@g.ntu.edu.tw") !== -1) {
                  return Promise.resolve();
                }
                return Promise.reject(new Error('!'));
              },
            })
          ]}
        >
          <Input onChange={onChangeEmail}/>
        </Form.Item>

        <Form.Item
          name="password"
          label=""
          rules={[
            {
              required: true,
              message: '!',
            },
          ]}
          hasFeedback
        >
          <Input.Password onChange={onChangePassword}/>
        </Form.Item>

        <Form.Item
          name="confirm password"
          label=""
          dependencies={['password']}
          hasFeedback
          rules={[
            {
              required: true,
              message: '!',
            },
            ({ getFieldValue }) => ({
              validator(_, value) {
                if (!value || getFieldValue('password') === value) {
                  return Promise.resolve();
                }

                return Promise.reject(new Error('!'));
              },
            }),
          ]}
        >
          <Input.Password onChange={onChangePassword}/>
        </Form.Item>

        <Form.Item
          name="nickname"
          label=""
          rules={[
            {
              required: true,
              message: '!',
              whitespace: true,
            },
          ]}
        >
          <Input onChange={onChangeNickname}/>
        </Form.Item>

        <Form.Item label=""  tooltip="" extra=" or  g suit ">
          <Row gutter={8}>
            <Col span={12}>
              <Form.Item
                name="captcha"
                noStyle

                rules={[
                  {
                    required: true,
                    message: '!',
                  },
                ]}
              >
                <Input onChange={onChangeCaptcha}/>
              </Form.Item>
            </Col>
            <Col span={12}>
              <Button onClick={getCode}></Button>
            </Col>
          </Row>
        </Form.Item>

        <Form.Item         {...tailFormItemLayout}>
          <Space>      
            <button htmlType="submit" className="button-style-3" onClick>
              
            </button>
            <p>
            </p>
            <a onClick={onClickLogin} class="underline" style={{ color:"#003D79"}}> ?!</a>
          </Space>
        </Form.Item>
        </Form>
      </Card>
    </div>     

    </div>
    </>
  );
};

export default Register;