import {Result, Button} from "antd";
import {SmileOutlined} from "@ant-design/icons"

const UnSelectDepartment = () =>{
	return(
		<div style={{paddingTop:"100px"}}>
		<Result
			icon={<SmileOutlined twoToneColor="red"/>}
			title="!"
			extra={<div></div>}
		/>
		</div>
	)
}

export default UnSelectDepartment;