import { useState, useRef } from 'react';
import { Form, Input, Button, Checkbox, Space, Card, message } from 'antd';
import { UserOutlined, LockOutlined } from '@ant-design/icons';
import { getMail, loginCheck } from '../api/User';
import Loading from "./Loading";
import sha256 from 'crypto-js/sha256'

const Login = ({ setMemberName, setIsLogin, setLoginOrRegister, setMemberMail ,setLoginOrForgetPassword, modal}) => {
	const [email, setEmail] = useState("initialState");
	const [password, setPassword] = useState("initialState");
  	// const modal =  useRef(null);

	const Submit = async (values) => {
		var thisModal = modal.current;
		thisModal.style.display = "block";
		const hashDigest = sha256(values.password)
		let hash = ''
		for (let i=0; i<hashDigest.words.length; i++) {
		  hash += hashDigest.words[i].toString()
		}
		//
		const data = await loginCheck(values.username, hash)
		if(data.access){
			message.success("")
			setIsLogin(true);
			setMemberName(values.username);
			const mail = await getMail(values.username);
			setMemberMail(mail)
		}
		else{
			message.error("")
		}
      	thisModal.style.display = "none";
	};


	const onChange_Email = (e) => {
		setEmail(e.target.value)
	}
	const onChange_Password = (e) => {
		setPassword(e.target.value)
	}

	const onClickRegister = (e) => {
		e.preventDefault();
		setLoginOrRegister("register")
	}

	const onClickForgetPassword = (e) => {
		e.preventDefault();
		setLoginOrForgetPassword("forgetPassword")
	}

	return (
		<>
		<div className="site-card-border-less-wrappe">
			<div className="card" style={{margin:"auto"}}>
			<Card title="" bordered={true} style={{ width: 300 }}>
				<Form
					name="normal_login"
					className="login-form"
					initialValues={{
						remember: true,
					}}
					onFinish={Submit}
				>
					<Form.Item
						name="username"
						rules={[
							{
								required: true,
								message: '!',
								whitespace: true,
							},
						]}
					>
						<Input
							prefix={<UserOutlined className="site-form-item-icon" />}
							placeholder=""
							onChange={onChange_Email}
						/>
					</Form.Item>
					<Form.Item
						name="password"
						rules={[
							{
								required: true,
								message: '!',
							},
						]}
					>

						<Input
							prefix={<LockOutlined className="site-form-item-icon" />}
							type="password"
							placeholder=""
							onChange={onChange_Password}
						/>
					</Form.Item>
					{/* <Form.Item>
						<Form.Item name="remember" valuePropName="checked" noStyle>
							<Checkbox></Checkbox>
						</Form.Item>
					</Form.Item> */}

					<Form.Item>
						<Space>
							<button htmlType="submit" className="button-style-3" onClick>
								
							</button>
							
							<div>
								<a onClick={onClickRegister} class="underline" style={{ color:"#003D79"}}> </a>
								<br></br>
								<a onClick={onClickForgetPassword} class="underline" style={{ color:"#003D79"}}></a>
							</div>

						</Space>
					</Form.Item>
				</Form>
			</Card>
			</div>
		</div>
		{/* <Loading modalRef={modal}/> */}
		</>
	);
};

export default Login;