// import Chart from 'chart.js/auto';
import {Line } from 'react-chartjs-2'

import { useEffect, useState } from 'react';
const ApplyPeopleChart = ({transApplyNum, doubleApplyNum, minorApplyNum, deviceWidth}) => {
	// 102~1109
	// tool:  https://www.colorhexa.com/

	const [chartWidth, setChartWidth] = useState(0);

	useEffect(() => {
		console.log(deviceWidth)
		if(deviceWidth > 750 )
			setChartWidth(600)
		else
			setChartWidth(360)
	}, []);

	useEffect(()=>{
		if(transApplyNum.length < 5){
			for(let i = 0 ; i < transApplyNum.length-5; i++){
				transApplyNum.push(0)
			}
		}
	},[])

	const data = {
		labels: ['107', '108', '109', '110', '111'],
		datasets: [
			{
				label: '',
				data: transApplyNum,
				fill: false,
				backgroundColor: '#B15BFF',
				borderColor: '#B15BFF',
			},
			{
				label: '',
				data: doubleApplyNum,
				fill: false,
				backgroundColor: '#5BC5DB',
				borderColor: '#5BC5DB',
			},
			{
				
				label: '',
				data: minorApplyNum,
				fill: false,
				backgroundColor: '#f58155',
				borderColor: '#f58155',
			},			
		],
	};

	if(minorApplyNum.length === 0){
		data.datasets[2].label = ""
	}
	if(doubleApplyNum.length === 0){
		data.datasets[1].label = ""
	}
	if(transApplyNum.length === 0){
		data.datasets[0].label = ""
	}

	const options = {
		// doc:https://www.chartjs.org/docs/latest/axes/cartesian/linear.html#grace
		maintainAspectRatio: false,
		responsive:true,
		scales: {
		 	y: {
		 		beginAtZero: true,
				 ticks: {
                    // Include a dollar sign in the ticks
                    callback: function(value, index, ticks) {
                        return value + '' ;
                    }
				}
			}
		},
		plugins: {
            legend: {
                labels: {
					// doc: https://www.chartjs.org/docs/latest/general/fonts.html
                    font: {
                        size: 15,
						lineHeight: 15,
						family: "Abel",
						weight: "bold"
                    }

                },
            },
			// doc: https://www.chartjs.org/docs/latest/configuration/title.html
			title: {
				// doc: https://www.chartjs.org/docs/latest/general/fonts.html
				font: {
					size: 20,
					lineHeight: 15,
					family: "Abel",
					weight: "bold"
				}
			},

        },		
	};


	return (
		<div style={{margin:"auto",maxWidth:500}}>
			{/* <div className='header'>
			<h1 className='title'></h1> */}
			<div className='links'>
			</div>
			{/* </div> */}
{/* redraw  */}
			{/* <Line data={data} options={options} width={deviceWidth-50} height={300}/> */}
			{/* option  responsive  ture */}
			<Line data={data} options={options} height={300}/>
		</div>
		
	)

}

export default ApplyPeopleChart;