import { Descriptions } from "antd";

const TransRequirement = ({transRequirement}) => {
	const newLine = (text)=>{
		try
		{
			text = text.replace("\n","\n\n")
			return text
		}
		catch{
			return text
		}
	}
	
	return (
	transRequirement
	?
	<div className="requirement">
		<Descriptions
		// title=""
		bordered
		size="default"
		style={{marginTop:"-50px"}}
		// column={{ xxl: 4, xl: 3, lg: 3, md: 3, sm: 2, xs: 1 }}
		>
		{/* <Descriptions.Item label="">{transRequirement.dep_id}</Descriptions.Item>
		<Descriptions.Item label="">{transRequirement.dep_name}</Descriptions.Item>		 */}
		<Descriptions.Item label="" style={{whiteSpace: "pre-line"}}>{transRequirement.accept_number}</Descriptions.Item>
		<Descriptions.Item label="" style={{whiteSpace: "pre-line"}}>{newLine(transRequirement.qualification)}</Descriptions.Item>
		<Descriptions.Item label="" style={{whiteSpace: "pre-line"}}>{newLine(transRequirement.admission_requirement)}</Descriptions.Item>
		<Descriptions.Item label="" style={{whiteSpace: "pre-line"}}>{newLine(transRequirement.other)}</Descriptions.Item>
		<Descriptions.Item label="" style={{whiteSpace: "pre-line"}}>{newLine(transRequirement.accept_second_priority)}</Descriptions.Item>
		<Descriptions.Item label="" style={{whiteSpace: "pre-line"}}>
		{newLine(transRequirement.notes)}
		</Descriptions.Item>
		</Descriptions>
	</div>
	:
	<></>
  )
}

export default TransRequirement;