import { Descriptions } from "antd";

const MinorRequirement = ({minorRequirement}) => {
 	const newLine = (text)=>{
		try
		{
			text = text.replace("\n","\n\n")
			return text
		}
		catch{
			return text
		}
	}
	
	return (
	minorRequirement
	?
	<div className="requirement">
		<Descriptions
		// title=""
		style={{marginTop:"-50px"}}
		bordered
		column={{ xxl: 4, xl: 3, lg: 3, md: 3, sm: 2, xs: 1 }}
		>
		{/* <Descriptions.Item label="">{minorRequirement.dep_id}</Descriptions.Item>
		<Descriptions.Item label="">{minorRequirement.dep_name}</Descriptions.Item>		 */}
		<Descriptions.Item label="" style={{whiteSpace: "pre-line"}}>{minorRequirement.accept_number}</Descriptions.Item>
		<Descriptions.Item label="" style={{whiteSpace: "pre-line"}}>{newLine(minorRequirement.qualification)}</Descriptions.Item>
		<Descriptions.Item label="" style={{whiteSpace: "pre-line"}}>{newLine(minorRequirement.admission_requirement)}</Descriptions.Item>
		<Descriptions.Item label="" style={{whiteSpace: "pre-line"}}>{newLine(minorRequirement.other)}</Descriptions.Item>
		<Descriptions.Item label="" style={{whiteSpace: "pre-line"}}>{newLine(minorRequirement.min_received_credit)}</Descriptions.Item>
		<Descriptions.Item label="" style={{whiteSpace: "pre-line"}}>{newLine(minorRequirement.compulsory)}</Descriptions.Item>
		<Descriptions.Item label="" style={{whiteSpace: "pre-line"}}>{newLine(minorRequirement.elective)}</Descriptions.Item>
		<Descriptions.Item label="" style={{whiteSpace: "pre-line"}}>
		{newLine(minorRequirement.notes)}
		</Descriptions.Item>
		</Descriptions>
	</div>
	:
	<></>
  )
}

export default MinorRequirement;