import { Descriptions } from "antd";

const DoubleRequirement = ({doubleRequirement}) => {
	const newLine = (text)=>{
		try
		{
			text = text.replace("\n","\n\n")
			return text
		}
		catch{
			return text
		}
	}

	return (
	doubleRequirement
	?
	<div className="requirement">
		<Descriptions
		// title=""
		style={{marginTop:"-50px"}}
		bordered
		column={{ xxl: 4, xl: 3, lg: 3, md: 3, sm: 2, xs: 1 }}
		>
		{/* <Descriptions.Item label="">{doubleRequirement.dep_id}</Descriptions.Item>
		<Descriptions.Item label="" >{doubleRequirement.dep_name}</Descriptions.Item>		 */}
		<Descriptions.Item label="" style={{whiteSpace: "pre-line"}}>{newLine(doubleRequirement.accept_number)}</Descriptions.Item>
		<Descriptions.Item label="" style={{whiteSpace: "pre-line"}}>{newLine(doubleRequirement.qualification)}</Descriptions.Item>
		<Descriptions.Item label="" style={{whiteSpace: "pre-line"}}>{newLine(doubleRequirement.admission_requirement)}</Descriptions.Item>
		<Descriptions.Item label="" style={{whiteSpace: "pre-line"}}>{newLine((doubleRequirement.other))}</Descriptions.Item>
		<Descriptions.Item label="" style={{whiteSpace: "pre-line"}}>
		{newLine(doubleRequirement.notes)}
		</Descriptions.Item>
		</Descriptions>
	</div>
	:
	<></>
  )
}

export default DoubleRequirement;